<template>
  <div
    class="tile-over-container"
    @mousedown.self="mousedown"
    @mousemove.self="mousemove"
    @mouseleave.self="mouseleave"
  >
    <div
      v-if="press && tileActive.isShow"
      class="tile-over"
      :class="[
        `tile-${first}`,
        `tile-over-position-${tileActive.x}-${tileActive.y}`,
      ]"
    >
      <div class="tile-inner">{{ first }}</div>
    </div>
  </div>
</template>

<script>
import { onMounted, onBeforeUnmount, ref } from "vue";

export default {
  name: "TileOver",
  props: {
    tiles: {
      type: Array,
      required: true,
    },
    ready: {
      type: Boolean,
      required: true,
    },
    first: {
      type: Number,
      default() {
        return 2;
      },
    },
    secondary: {
      type: Number,
      default() {
        return 2;
      },
    },
  },
  emits: ["myEvent"],
  setup(props, context) {
    const press = ref(false);
    const tileActive = ref({ x: 0, y: 1, isShow: false });

    /**
     * Событие нажатия левой кнопки мыши на игровое поле
     * @param e
     */
    const mousedown = function (e) {
      if (!props.ready) return;
      /**
       * Пропускаем любые нажатия мыши кроме левой кнопки
       */
      if (e.which !== 1 && e.button !== 1) {
        return;
      }

      press.value = true;

      /**
       * Вызываем событие перемещение при нажатии левой кнопки мыши
       */
      mousemove(e);
    };

    /**
     * Событие отпускания левой кнопки мыши
     * @param e
     */
    const mouseup = function (e) {
      /**
       * Пропускаем любые нажатия мыши кроме левой кнопки
       */
      if (e.which !== 1 && e.button !== 1) {
        return;
      }

      press.value = false;

      if (tileActive.value.isShow) {
        tileActive.value.isShow = false;

        /**
         * Сообщаем родителю о необходимости добавить ячейку на игровое поле
         */
        context.emit(
          "myEvent",
          tileActive.value.x,
          tileActive.value.y,
          props.first
        );
      }
    };

    /**
     * Событие перемещения курсора мыши
     * @param e
     */
    const mousemove = function (e) {
      if (!press.value) return;

      /**
       * Пропускаем любые нажатия мыши кроме левой кнопки
       */
      if (e.which !== 1 && e.button !== 1) {
        return;
      }

      const x = e.offsetX;

      const width = 500;
      const size = 5;

      /**
       * Определяем активную колонку
       */
      tileActive.value.x = Math.trunc((x * size) / width) + 1;

      /**
       * Формируем список занятых строк в активной колонке
       * @type {number[]}
       */
      const filtered = props.tiles
        .filter((tile) => tile.x === tileActive.value.x)
        .map((tile) => tile.y);

      filtered.push(0);

      /**
       * Определяем активную первую незанятую строку
       */
      tileActive.value.y = Math.max(...filtered) + 1;

      /**
       * Отсечка в случае если колонку переполнена
       */
      if (tileActive.value.y > 5) {
        tileActive.value.isShow = false;

        return;
      }

      tileActive.value.isShow = true;
    };

    /**
     * Событие покидания курсора мыши игрового поля
     */
    const mouseleave = function () {
      tileActive.value.x = 0;
      tileActive.value.y = 0;
      tileActive.value.isShow = false;
    };

    onMounted(() => {
      window.addEventListener("mouseup", mouseup);
    });

    onBeforeUnmount(() => {
      window.removeEventListener("mouseup", mouseup);
    });

    return {
      tileActive,
      press,
      mousemove,
      mouseleave,
      mousedown,
      mouseup,
    };
  },
};
</script>

<style lang="scss">
@import "../assets/style/helpers";
@import "../assets/style/variables";

.tile-over-container {
  position: absolute;
  z-index: 10;
  top: 0;
  left: 0;

  cursor: pointer;
  user-select: none;
  border-radius: $tile-border-radius * 2;
  width: $container-width;
  height: $container-width;
  box-sizing: border-box;
}

.tile-over {
  width: ceil($tile-size);
  z-index: 9;

  transition: all 0.2s;

  background: $tile-color-over-2;

  .tile-inner {
    width: ceil($tile-size);
    height: ceil($tile-size);
    line-height: ceil($tile-size);

    background: $tile-color-over;
  }

  // Build over position classes
  @for $x from 1 through $grid-row-cells {
    @for $y from 1 through $grid-row-cells {
      &.tile-over-position-#{$x}-#{$y} {
        $xPos: ceil(($tile-size + $grid-spacing) * ($x - 1) + $grid-spacing);
        $yPos: ceil(($tile-size + $grid-spacing) * ($y - 1) + $grid-spacing);
        @include transform(translate($xPos, $yPos));

        height: ceil(
          ($tile-size + $grid-spacing) * ($grid-row-cells - $y) + $tile-size
        );
      }
    }
  }
}
</style>
