<template>
  <div class="tile-container">
    <div
      v-for="tile of tiles"
      :key="tile.id"
      class="tile"
      :class="[
        `tile-${tile.value}`,
        `tile-position-${tile.x}-${tile.y}`,
        { 'tile-merged': tile.merged },
        { 'tile-new': tile.new },
      ]"
    >
      <div class="tile-inner">{{ tile.value }}</div>
    </div>
  </div>
  <!--  <NewGame v-if="empty.length === 0 && tiles.length === 0" @click="start" />-->
  <!--  <GameOver v-if="empty.length === 0 && tiles.length > 0" @click="start" />-->

  <TileOver
    :tiles="tiles"
    :first="first"
    :secondary="secondary"
    :ready="ready"
    @my-event="add"
  />

  <TileNext :first="first" :secondary="secondary" :third="third" :move="move" />
</template>

<script>
import { onMounted, reactive, ref } from "vue";
import TileOver from "@/components/TileOver";
import TileNext from "@/components/TileNext";

export default {
  name: "Tile",
  emits: ["score"],
  components: { TileNext, TileOver },
  setup(props, context) {
    const delay = 150;
    const first = ref(0);
    const secondary = ref(0);
    const third = ref(0);
    const move = ref(false);
    const index = ref(1);
    const tiles = ref([]);
    const existTiles = reactive([2, 4]);
    const ready = ref(false);

    const getClass = function (tile) {
      const s = "";
      s.concat(" tile-position-").concat(tile.x).concat("-").concat(tile.y);

      return s;
    };

    const random = function () {
      return existTiles[Math.floor(Math.random() * existTiles.length)];
    };

    const contains = function (item) {
      return existTiles.indexOf(item) !== -1;
    };

    const add = async function (x, y, value) {
      ready.value = false;

      clear();

      third.value = random();

      move.value = true;

      tiles.value.push({
        id: index.value,
        x: x,
        y: y,
        value: value,
        new: true,
        merged: false,
      });

      index.value = index.value + 1;

      moveUp();

      await sleep(delay);
      move.value = false;
      first.value = secondary.value;
      secondary.value = third.value;

      let repeat = false;

      do {
        repeat = await merge();
        moveUp();
      } while (repeat);

      ready.value = true;
    };

    const moveUp = function () {
      tiles.value.forEach((tile) => {
        tile.y = Math.max(
          tiles.value
            .slice()
            .filter((t) => t.x === tile.x && t.y < tile.y)
            .map((t) => t.y)
            .push(tile.y)
        );
      });
    };

    const sleep = function (ms) {
      return new Promise((resolve) => setTimeout(resolve, ms));
    };

    const merge = async function () {
      for (const tile of tiles.value) {
        if (!tile.new && !tile.merged) continue;

        const mergeTiles = tiles.value.filter(
          (t) =>
            t.value === tile.value &&
            ((t.x - 1 === tile.x && t.y === tile.y) ||
              (t.x + 1 === tile.x && t.y === tile.y) ||
              (t.x === tile.x && t.y - 1 === tile.y) ||
              (t.x === tile.x && t.y + 1 === tile.y))
        );

        if (mergeTiles.length === 0) continue;

        await sleep(delay);

        /**
         * Частный случай, когда ячейка поглощаемая сверху
         */
        if (
          mergeTiles.length === 1 &&
          mergeTiles[0].x === tile.x &&
          mergeTiles[0].y === tile.y - 1
        ) {
          mergeTiles.forEach((t) => {
            t.value = tile.value * 2;
            t.merged = true;
            t.new = true;

            tile.y = t.y;
            tile.x = t.x;
            tile.merged = false;
            tile.new = false;

            if (!contains(t.value)) {
              existTiles.push(t.value);
            }
          });

          context.emit("score", tile.value * 2);

          await sleep(delay);

          tiles.value = tiles.value.filter((t) => t.id !== tile.id);

          return true;
        }

        tile.value = tile.value * Math.pow(2, mergeTiles.length);
        tile.merged = true;

        if (!contains(tile.value)) {
          existTiles.push(tile.value);
        }

        mergeTiles.forEach((t) => {
          t.x = tile.x;
          t.y = tile.y;
        });

        context.emit("score", tile.value);

        await sleep(delay);

        tiles.value = tiles.value.filter(
          (t) => !mergeTiles.map((s) => s.id).includes(t.id)
        );

        return true;
      }

      return false;
    };

    const clear = function () {
      tiles.value = tiles.value.map((tile) => {
        tile.new = false;
        tile.merged = false;

        return tile;
      });
    };

    onMounted(() => {
      first.value = random();
      secondary.value = random();
      ready.value = true;
    });

    return {
      tiles,
      existTiles,
      first,
      secondary,
      third,
      move,
      ready,
      add,
      moveUp,
      merge,
      getClass,
    };
  },
};
</script>

<style lang="scss">
@import "../assets/style/helpers";
@import "../assets/style/variables";

.tile-container {
  position: absolute;
  z-index: 2;

  .tile {
    transition: all 0.2s;
  }
}

.tile {
  &,
  .tile-inner {
    width: ceil($tile-size);
    height: ceil($tile-size);
    line-height: ceil($tile-size);
  }

  &.tile-merged {
    z-index: 3;
  }

  // Build position classes
  @for $x from 1 through $grid-row-cells {
    @for $y from 1 through $grid-row-cells {
      &.tile-position-#{$x}-#{$y} {
        $xPos: floor(($tile-size + $grid-spacing) * ($x - 1));
        $yPos: floor(($tile-size + $grid-spacing) * ($y - 1));
        @include transform(translate($xPos, $yPos));
      }
    }
  }
}
.tile,
.tile-over {
  position: absolute;

  .tile-inner {
    border-radius: $tile-border-radius;

    background: $tile-color;
    text-align: center;
    font-weight: bold;
    z-index: 10;
  }
}

@for $value from 1 through 10 {
  $x: pow(2, $value);
  .tile-#{$x} > .tile-inner {
    @if ($x < 128) {
      font-size: 55px;
    } @else if($x >= 128) {
      font-size: 36px;
    }
  }
}
</style>
