<template>
  <div class="container">
    <Header :score="score" :best="best" />
    <div class="game-container">
      <Grid />
      <Tile ref="tile" @score="updateScore" />
    </div>
  </div>
</template>

<script>
import Header from "./components/Header";
import Grid from "./components/Grid";
import Tile from "./components/Tile";
import { onBeforeMount, onMounted, ref } from "vue";
export default {
  name: "App",
  components: { Tile, Grid, Header },
  setup() {
    const score = ref(0);
    const best = ref(0);
    const ready = ref(false);

    const updateScore = function (value) {
      score.value = score.value + value;

      if (best.value < score.value) {
        best.value = score.value;
        localStorage.setItem("best", best.value + "");
      }
    };

    onBeforeMount(() => {
      const _best = localStorage.getItem("best");

      if (_best) {
        best.value = JSON.parse(_best);
      }
    });

    onMounted(() => {
      ready.value = true;
    });

    return { score, best, ready, updateScore };
  },
};
</script>

